import React from "react";
import logo from "../../styles/assets/images/Logo/Justtry logo-whitebg.png";
import instagram from "../../styles/assets/images/Socialicons/instagram.png";
import facebook from "../../styles/assets/images/Socialicons/facebook.png";
import whatsapp from "../../styles/assets/images/Socialicons/whatsapp.png";
import email from "../../styles/assets/images/Socialicons/email.png";
import twitter from "../../styles/assets/images/Socialicons/twitter.png";

const Footer = () => {
  return (
    <footer className='footer'>
      <div className='container grid grid--footer'>
        <div className='logo-col'>
          <a href='#' className='footer-logo'>
            <img
              className='logo'
              alt='Justtry logo'
              src={logo}
            />
          </a>
        </div>

        <div className='address-col'>
          <p className='description'>Contact us</p>
          <address className='contacts'>
            <p className='address'>
              3A/1, Patcharisikara Thoppu 1st Street, Old Kuyavar Palayam Road,
              Madurai - 625 009.
            </p>
            <p>
              <a className='footer-link' href='tel:415-201-6370'>
                +91 - 9380543009
              </a>
              <br />
              <a className='footer-link' href='mailto:flipflopinfotech@gmail.com'>
                flipflopinfotech@gmail.com
              </a>
            </p>
          </address>
        </div>

        <nav className='nav-col'>
          <p className='description'>Company</p>
          <ul className='footer-nav'>
            <li>
              <a className='footer-link' href='#'>
                About Us
              </a>
            </li>
            <li>
              <a className='footer-link' href='#'>
                For Business
              </a>
            </li>
            <li>
              <a className='footer-link' href='#'>
                Client partners
              </a>
            </li>
          </ul>
        </nav>

        <nav className='nav-col'>
          <p className='description'>Resources</p>
          <ul className='social-links'>
            <li>
              <a className='footer-link' href='#'>
                <img
                  className='social-icon'
                  src={instagram}
                  alt='Instagram'
                />
              </a>
            </li>
            <li>
              <a className='footer-link' href='#'>
                <img
                  className='social-icon'
                  src={facebook}
                  alt='facebook'
                />
              </a>
            </li>
            <li>
              <a className='footer-link' href='#'>
                <img
                  className='social-icon'
                  src={whatsapp}
                  alt='whatsapp'
                />
              </a>
            </li>
            <li>
              <a className='footer-link' href='#'>
                <img
                  className='social-icon'
                  src={email}
                  alt='email'
                />
              </a>
            </li>
            <li>
              <a className='footer-link' href='#'>
                <img
                  className='social-icon'
                  src={twitter}
                  alt='twitter'
                />
              </a>
            </li>
          </ul>

          <p className='copyright'>
            Copyright &copy; 2023 by Justtry Technology Private Limited.
          </p>
          <p className='copyright'>All rights reserved.</p>
        </nav>
      </div>
    </footer>
  );
};

export default Footer;
