import React, { useEffect } from "react";
import logo from "../../styles/assets/images/Hero-logo.avif";
import { bottomAnimation, rightAnimation } from "../../core/const";

const MoveForward = () => {
  useEffect(() => {
    rightAnimation();
    bottomAnimation();
  }, []);

  return (
    <section className='section-cta' id='talktous'>
      <div className='container'>
        <div className='cta'>
          <div className='bottomAnimation cta-text-box'>
            <h2 className='heading-secondary'>
              READY TO MOVE FORWARD WITH JUSTTRY TEAM?
            </h2>
            <p className='cta-text'>
              Increase efficency, productivity, and business performance with
              Flipflop various products that have been used by tens of thousands
              of businesses in the word.
            </p>

            <form className='cta-form' action='#'>
              <div>
                <label for='full-name'>Full Name</label>
                <input
                  id='full-name'
                  type='text'
                  placeholder='John Smith'
                  required
                />
              </div>

              <div>
                <label for='email'>Email address</label>
                <input
                  id='email'
                  type='email'
                  placeholder='me@example.com'
                  required
                />
              </div>

              <div>
                <label for='select-where'>Where did you hear from us?</label>
                <select id='select-where' required>
                  <option value=''>Please choose one option:</option>
                  <option value='friends'>Friends and family</option>
                  <option value='youtube'>YouTube video</option>
                  <option value='podcast'>Podcast</option>
                  <option value='ad'>Facebook ad</option>
                  <option value='others'>Others</option>
                </select>
              </div>

              <button className='btn btn--form'>Sign up now</button>
            </form>
          </div>

          <div
            className='rightAnimation cta-img-box'
            role='img'
            aria-label='Some Tech Design'>
            <img src={logo} alt='' />
          </div>
        </div>
      </div>
    </section>
  );
};

export default MoveForward;
